import React from 'react';
import { Field, reduxForm } from 'redux-form';
import { connect } from 'react-redux';
import { compose } from 'redux';
import { withRouter } from 'react-router-dom';
import cn from 'classnames';
import s from './Dialogs.module.scss';
import { getExactDialog, startChatting } from '../../redux/dialogs-reducer';
import Loader from '../Loader/Loader';
import { sendMessage } from '../../redux/dialogs-reducer';


class Dialog extends React.Component {
  componentDidMount() {
    this.props.getExactDialog(this.props.match.params.id);
  }

  render() {
    const userID = this.props.match.params.id;
    const addNewMessage = (formData) => {
      this.props.sendMessage(userID, formData);
    };

    return (
      <div className={s.dialogs}>
        <div className={s.dialogsItems}>
          {this.props.isFetching ? <Messages {...this.props} /> : <Loader /> }
        </div>
        <div className={s.messages}>
          <div>
            <DialogFormRedux onSubmit={addNewMessage} />
          </div>
        </div>
      </div>
    );
  }
}

const Messages = (props) => {
  const messages = props.currentDialog.map((obj) => (
    <>
      <div>
        From:
        {obj.senderName}
      </div>
      <div className={cn({ [s.notViewed]: !obj.viewed })}>{obj.body}</div>
    </>
  ));
  return (
    messages
  );
};

const DialogForm = (props) => (
  <form onSubmit={props.handleSubmit}>
    <div>
      <Field name="body" component="textarea" type="text" />
    </div>
    <div>
      <button type="submit">Send</button>
    </div>
  </form>
);
const DialogFormRedux = reduxForm({ form: 'dialog' })(DialogForm);

const mapStateToProps = (state) => ({
  isFetching: state.dialogsPage.isFetching,
  currentDialog: state.dialogsPage.currentDialog,
  userID: state.dialogsPage,
});

export default compose(connect(mapStateToProps, { getExactDialog, startChatting, sendMessage }), withRouter)(Dialog);
