import React from 'react';
import { Field, reduxForm } from 'redux-form';
import s from './MyPosts.module.scss';
import Post from './Post/Post';


const MyPosts = (props) => {
  const addPost = (value) => {
    props.addPost(value.post);
  };
  const postsElements = props.posts.map((p) => <Post message={p.message} like={p.likesCount} />);
  return (
    <div className={s.postBlock}>
      {props.isOwner
        ? <div className={s.myPosts}>My posts</div>
        : <div className={s.myPosts}>User posts</div>}
      {props.isOwner && <PostFormRedux onSubmit={addPost} />}
      <div className={s.posts}>
        {postsElements}
      </div>
    </div>
  );
};

const PostForm = (props) => (
  <div>
    <form onSubmit={props.handleSubmit}>
      <div>
        <Field placeholder="type your post here" name="post" component="textarea" />
      </div>
      <div>
        <button type="submit" className={s.btn}>Add post</button>
      </div>
    </form>
  </div>
);
const PostFormRedux = reduxForm({ form: 'postForm' })(PostForm);

export default MyPosts;
