import { dialogsAPI } from '../API/API';

const ADD_RECIPIENT_INFO = 'ADD_RECIPIENT_INFO';
const UPLOAD_DIALOG = 'UPLOAD_DIALOG';
const IS_FETCHING = 'ON_FETCHING';
const SEND_MESSAGE = 'SEND_MESSAGE';


const initialState = {
  dialogs: [],
  currentDialog: [],
  isFetching: false,
};

const dialogsReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_RECIPIENT_INFO:
      return {
        ...state,
        dialogs: [...state.dialogs, ...action.arrayInfo],
      };
    case UPLOAD_DIALOG:
      return {
        ...state,
        currentDialog: action.messages.items,
      };
    case IS_FETCHING:
      return {
        ...state, isFetching: action.boolean,
      };
    case SEND_MESSAGE:
      return {
        ...state,
        currentDialog: [...state.currentDialog, action.message],
      };
    default:
      return state;
  }
};

export const getRecipientInfo = (arrayInfo) => ({ type: ADD_RECIPIENT_INFO, arrayInfo });
export const setDialog = (messages) => ({ type: UPLOAD_DIALOG, messages});
export const isFetching = (boolean) => ({ type: IS_FETCHING, boolean});
export const sendMessageAC = (message) => ({ type: SEND_MESSAGE, message});



export const getDialogs = () => async (dispatch) => {
  const response = await dialogsAPI.getDialogs();
  dispatch(getRecipientInfo(response.data));
};

export const getExactDialog = (id) => async (dispatch) => {
  const response = await dialogsAPI.geExactDialog(id);
  await dispatch(setDialog(response.data));
  dispatch(isFetching(true));
};
export const startChatting = (id) => async (dispatch) => {
  const response = await dialogsAPI.startChatting(id);
};

export const sendMessage = (userId, message) => async (dispatch) => {
  const response = await dialogsAPI.sendMessage(userId, message);
  if (response.data.resultCode === 0) {
    dispatch(sendMessageAC(response.data.data.message));
    dispatch(startChatting(userId));
  }
};



export default dialogsReducer;
